<template>
  <div class="login-box">
    <div> kanban</div>
    <div><Input v-model="value" placeholder="" style="width: 300px"></Input></div>
    <div><Button type="primary" @click="login"></Button></div>
  </div>
</template>
<style>
@import './login.css';
</style>
<script>
    export default {
        data () {
            return {
                value: ''
            }
        },
        methods: {
            login () {
                this.$router.push('/kanban')
            }
        }
    }
</script>


